export default "<template>\n  <div class=\"pro-form-demo\">\n    <ProForm \n      ref=\"proFormRef\"\n      :formProps=\"formProps\"\n      className=\"pro-form-demo\"\n      :formItems=\"formItems\"\n      :submitter=\"submitter\"\n      :initialValues=\"initialValues\"\n      @onFinish=\"onFinish\"\n      @onError=\"onError\"\n      @onReset=\"onReset\"\n    >\n      <!-- 3 -->\n      <!-- start -->\n      <template #name3=\"{ form, formItem }\">\n        <el-input v-model=\"form.name3\" v-bind=\"formItem.fieldProps\"></el-input>\n      </template>\n      <!-- end -->\n      <!--  -->\n      <!-- start -->\n      <template #date=\"{ form }\">\n        <el-form-item label=\"1\" required>\n          <el-col :span=\"11\">\n            <el-form-item prop=\"date1\">\n              <el-date-picker type=\"date\" placeholder=\"\" v-model=\"form.date1\" style=\"width: 100%;\"></el-date-picker>\n            </el-form-item>\n          </el-col>\n          <el-col class=\"line\" :span=\"2\">-</el-col>\n          <el-col :span=\"11\">\n            <el-form-item prop=\"date2\">\n              <el-time-picker placeholder=\"\" v-model=\"form.date2\" style=\"width: 100%;\"></el-time-picker>\n            </el-form-item>\n          </el-col>\n        </el-form-item>\n      </template>\n      <!-- end -->\n      <!--  -->\n      <!-- start -->\n      <template #datetime=\"{ form }\">\n        <el-form-item label=\"2\">\n          <el-col :span=\"11\">\n            <el-form-item prop=\"date3\">\n              <el-date-picker type=\"date\" placeholder=\"\" v-model=\"form.date3\" style=\"width: 100%;\"></el-date-picker>\n            </el-form-item>\n          </el-col>\n          <el-col class=\"line\" :span=\"2\">-</el-col>\n          <el-col :span=\"11\">\n            <el-form-item prop=\"date4\">\n              <el-time-picker placeholder=\"\" v-model=\"form.date4\" style=\"width: 100%;\"></el-time-picker>\n            </el-form-item>\n          </el-col>\n        </el-form-item>\n      </template>\n      <!-- end -->\n      <template #items=\"{ form }\">\n        <EditableProTable\n          :dataSource=\"form.items\"\n          :columns=\"columns\"\n          :editable=\"editable\"\n          name=\"items\"\n          :recordCreatorProps=\"recordCreatorProps\"\n          rowKey=\"id\"\n        />\n      </template>\n    </ProForm>\n  </div>\n</template>\n\n<script>\n  import { ProForm, EditableProTable } from 'element-ui-pro-components'\n\n  export default {\n    name: 'Demo1',\n    components: {\n      ProForm,\n      EditableProTable\n    },\n    computed: {\n      formItems() {\n        return [\n          { \n            label: '1',\n            prop: 'name1',\n            valueType: 'input',\n            rules: [\n              { required: true, message: '', trigger: 'blur' }\n            ]\n          },\n          {\n            renderLabel: () => <span style=\"color: #F56C6C\">2</span>,\n            prop: 'name2',\n            valueType: 'input',\n            fieldProps: {\n              placeholder: 'renderLabel'\n            }\n          },\n          {\n            label: '3',\n            prop: 'name3',\n            valueType: 'slot',\n            fieldProps: {\n              placeholder: 'valueType: slot ',\n              clearable: true\n            }\n          },\n          {\n            label: '4',\n            prop: 'name4',\n            renderField: ({ form, formItem }) => \n              <el-input \n                value={ form.name4 } \n                onInput={val => form.name4 = val } \n                attrs={ formItem.fieldProps }\n                on={ formItem.fieldEvents }\n                onClear={this.handleClear}\n              />\n            ,\n            fieldProps: {\n              placeholder: 'renderField ',\n              clearable: true,\n            },\n            fieldEvents: {\n              change: this.handleChange\n            }\n          },\n          {\n            label: '1',\n            prop: 'region1',\n            valueType: 'select',\n            fieldProps: {\n              clearable: true\n            },\n            options: [\n              { label: '', value: 'beijing' },\n              { label: '', value: 'shanghai' },\n            ],\n            initialValue: 'beijing',\n            rules: [\n              { required: true, message: '', trigger: 'change' }\n            ]\n          },\n          {\n            label: '2',\n            prop: 'region2',\n            valueType: 'select',\n            valueEnum: {\n              'beijing': '',\n              'shanghai': ''\n            },\n          },\n          {\n            label: '3',\n            prop: 'region3',\n            valueType: 'select',\n            valueEnum: new Map([\n              ['beijing', ''],\n              ['shanghai', '']\n            ]),\n          },\n          {\n            label: '4',\n            prop: 'region4',\n            valueType: 'select',\n            optionLoader: () => {\n              return new Promise(resolve => {\n                setTimeout(() => {\n                  resolve([\n                    { label: '', value: 'beijing' },\n                    { label: '', value: 'shanghai' },\n                  ])\n                }, 1000)\n              })\n            },\n          },\n          {\n            label: '',\n            prop: 'category',\n            valueType: 'checkbox-group',\n            options: [\n              { label: '/', value: '/' },\n              { label: '', value: '' },\n              { label: '', value: '' },\n              { label: '', value: '' },\n            ],\n            initialValue: [],\n            rules: [\n              { required: true, message: '', trigger: 'change' }\n            ]\n          },\n          {\n            label: '',\n            prop: 'resource',\n            valueType: 'radio-group',\n            options: [\n              { label: '', value: '' },\n              { label: '', value: '' }\n            ],\n            rules: [\n              { required: true, message: '', trigger: 'change' }\n            ]\n          },\n          {\n            label: '1',\n            prop: 'type',\n            valueType: 'cascader',\n            fieldProps: {\n              options: this.typeOptions,\n              'popper-class': 'cascader-popper'\n            },\n            rules: [\n              { required: true, message: '', trigger: 'change' }\n            ]\n          },\n          {\n            label: '2',\n            prop: 'type2',\n            valueType: 'cascader',\n            fieldProps: {\n              'popper-class': 'cascader-popper'\n            },\n            optionLoader: () => new Promise(resolve => {\n              setTimeout(() => {\n                resolve(this.typeOptions)\n              }, 2000)\n            })\n          },\n          {\n            label: 'I\\'m hide',\n            prop: 'hide',\n            valueType: 'input',\n            hideInForm: true\n          },\n          {\n            prop: 'date',\n            customSlot: true,\n          },\n          {\n            customSlot: 'datetime',\n          },\n          {\n            //  v-model  \n            renderFormItem: (form) => \n              <el-form-item label=\"3\">\n                <el-col span={11}>\n                  <el-form-item prop=\"date5\">\n                    <el-date-picker type=\"date\" placeholder=\"\" value={form.date5} on={ { input: val => form.date5 = val } } style=\"width: 100%;\"></el-date-picker>\n                  </el-form-item>\n                </el-col>\n                <el-col class=\"line\" span={2}>-</el-col>\n                <el-col span={11}>\n                  <el-form-item prop=\"date6\">\n                    <el-time-picker placeholder=\"\" value={form.date6} on={ { input: val => form.date6 = val } } style=\"width: 100%;\"></el-time-picker>\n                  </el-form-item>\n                </el-col>\n              </el-form-item>\n          },\n          {\n            prop: 'items',\n            label: '',\n            valueType: 'slot',\n            rules: [\n              { required: true, message: '' }\n            ],\n            initialValue: []\n          }\n        ]\n      },\n      submitter() {\n        const { loading } = this\n        return {\n          resetText: '',\n          submitText: '',\n          resetButtonProps: {\n            type: 'success',\n            plain: true,\n            style: {\n              'border-style': 'dashed'\n            }\n          },\n          submitButtonProps: {\n            loading\n          },\n        }\n      },\n      columns() {\n        return [\n          { \n            label: '',\n            prop: 'label',\n            valueType: 'input',\n            formItemProps: {\n              rules: [{ required: true, message: '', trigger: 'blur' }]\n            },\n          },\n          {\n            label: '',\n            prop: 'type',\n            valueType: 'select',\n            valueEnum: {\n              '1': '',\n              '2': '',\n              '3': '',\n            },\n            formItemProps: {\n              rules: [{ required: true, message: '', trigger: 'change' }]\n            },\n          },\n          {\n            width: 80,\n            label: '',\n            valueType: 'option',\n            fixed: 'right',\n            renderCell: (scope, action) => [\n              <el-button type=\"text\" onClick={() => this.onDelete(scope.row.id)}></el-button>,\n              <el-button type=\"text\" onClick={() => action.addEditRecord({ ...scope.row, id: Math.random().toString().slice(2, 10) })}></el-button>\n            ]\n          }\n        ]\n      },\n      editable() {\n        const { editableKeys } = this\n        return {\n          type: 'multiple',\n          editableKeys,\n          onChange: keys => this.editableKeys = keys,\n          actionRender: (row, config, defaultDoms) => {\n            return [defaultDoms.delete]\n          },\n        }\n      },\n\n    },\n    data() {\n      return {\n        formProps: {\n          'label-width': '110px',\n          rules: {\n            date1: [{ required: true, message: '', trigger: 'change'}],\n            date2: [{ required: true, message: '', trigger: 'change'}]\n          }\n        },\n        typeOptions: [\n          {\n            label: '',\n            value: 'ball',\n            children: [\n              { label: '', value: '1' },\n              { label: '', value: '2' },\n              { label: '', value: '3' },\n            ]\n          },\n          {\n            label: '',\n            value: 'other',\n            children: [\n              { label: '', value: '11' },\n              { label: '', value: '22' },\n              { label: '', value: '33' },\n            ]\n          },\n        ],\n        initialValues: {\n          date3: new Date(2026, 0, 1),\n          date4: Date.now(),\n          date5: '',\n          date6: '',\n        },\n        loading: false,\n        editableKeys: [],\n        recordCreatorProps: {\n          newRecordType: 'dataSource',\n          record: () => (\n            {\n              id: Math.random().toString().slice(2, 10),\n            }\n          )\n        },\n      }\n    },\n    methods: {\n      handleClear() {\n        console.log('clear')\n      },\n      handleChange(val) {\n        console.log('change', val)\n      },\n      onFinish(values) {\n        this.loading = true\n        console.log('values', values)\n        setTimeout(() => {\n          this.loading = false\n        }, 2000)\n      },\n      onError(errors) {\n        console.log('errors', errors)\n      },\n      onReset() {\n        const form = this.$refs.proFormRef.getForm()\n        console.log('form', form)\n      },\n      onDelete(recordKey) {\n        const formRef = this.$refs.proFormRef\n        const { items } = formRef.getForm()\n        console.log('items', items)\n        formRef.setFieldValue('items',\n          items.filter(item => item.id !== recordKey)\n        )\n      }\n    }\n  }\n</script>\n\n<style scoped>\n.pro-form-demo {\n  padding: 24px;\n}\n\n.line {\n  text-align: center;\n}\n</style>\n\n<style>\n.cascader-popper .el-cascader-menu__wrap {\n  height: 204px;\n}\n</style>\n";