export default "<template>\n  <div class=\"dialog-form-demo\">\n    <el-button @click=\"visible = true\"></el-button>\n    <DialogForm \n      ref=\"dialogFormRef\"\n      v-model=\"visible\"\n      width=\"960px\"\n      :renderTitle=\"renderTitle\"\n      :formItems=\"formItems\"\n      :grid=\"true\"\n      :rowProps=\"rowProps\"\n      :submitter=\"submitter\"\n      @onFinish=\"onFinish\"\n      @onError=\"onError\"\n      @onReset=\"onReset\"\n    >\n    </DialogForm>\n  </div>\n</template>\n\n<script>\n  import { DialogForm } from 'element-ui-pro-components'\n\n  export default {\n    name: 'Demo2',\n    components: {\n      DialogForm,\n    },\n    computed: {\n      formItems() {\n        return [\n          { \n            label: '1',\n            prop: 'name1',\n            valueType: 'input',\n            rules: [\n              { required: true, message: '', trigger: 'blur' }\n            ],\n            colProps: {\n              span: 8\n            }\n          },\n          { \n            label: '2',\n            prop: 'name2',\n            valueType: 'input',\n            rules: [\n              { required: true, message: '', trigger: 'blur' }\n            ],\n            colProps: {\n              span: 8\n            }\n          },\n          { \n            label: '3',\n            prop: 'name3',\n            valueType: 'input',\n            rules: [\n              { required: true, message: '', trigger: 'blur' }\n            ],\n            colProps: {\n              span: 8\n            }\n          },\n          { \n            label: '4',\n            prop: 'name4',\n            valueType: 'input',\n            rules: [\n              { required: true, message: '', trigger: 'blur' }\n            ],\n            colProps: {\n              span: 8\n            }\n          },\n          { \n            label: '5',\n            prop: 'name5',\n            valueType: 'input',\n            rules: [\n              { required: true, message: '', trigger: 'blur' }\n            ],\n            colProps: {\n              span: 8\n            }\n          },\n          { \n            label: '6',\n            prop: 'name6',\n            valueType: 'input',\n            rules: [\n              { required: true, message: '', trigger: 'blur' }\n            ],\n            colProps: {\n              span: 8\n            }\n          },\n        ]\n      },\n      submitter() {\n        const { loading } = this\n        return {\n          submitButtonProps: {\n            loading\n          },\n          customRender: (form, action, doms) => {\n            return [\n              doms[0],\n              <el-button props={{ type: 'danger' }} onClick={action.close}></el-button>,\n              <el-button props={{ type: 'info' }} onClick={action.reset}></el-button>,\n              <el-button props={{ type: 'warning' }} onClick={action.resetAllFields}></el-button>,\n              doms[1],\n              <el-button props={{ type: 'success' }} onClick={action.submit}></el-button>,\n              <el-button onClick={this.getFormRef}>getFormRef</el-button>,\n              <el-button onClick={this.getForm}>getForm</el-button>,\n              <el-button onClick={this.setFieldsValue}>setFieldsValue</el-button>,\n              <el-button onClick={this.setFieldValue}>setFieldValue</el-button>\n            ]\n          }\n        }\n      }\n    },\n    data() {\n      return {\n        visible: false,\n        renderTitle: () => <span style=\"color: #F56C6C\"></span>,\n        rowProps: { gutter: 20 },\n        loading: false\n      }\n    },\n    methods: {\n      onFinish(values) {\n        this.loading = true\n        console.log('values', values)\n        setTimeout(() => {\n          this.loading = false\n        }, 2000)\n      },\n      onError(errors) {\n        console.log('errors', errors)\n      },\n      onReset() {\n        const form = this.$refs.dialogFormRef.getForm()\n        console.log('form', form)\n      },\n      onClose() {\n        this.$refs.dialogFormRef?.close()\n      },\n      getFormRef() {\n        console.log(this.$refs.dialogFormRef?.getFormRef())\n      },\n      getForm() {\n        console.log(this.$refs.dialogFormRef?.getForm())\n      },\n      setFieldsValue() {\n        this.$refs.dialogFormRef?.setFieldsValue({\n          name1: 'hello world',\n          other: 'hello world'\n        })\n        this.getForm()\n      },\n      setFieldValue() {\n        this.$refs.dialogFormRef?.setFieldValue('name2', 'hello js')\n        this.getForm()\n      },\n    },\n  }\n</script>\n\n<style scoped>\n.dialog-form-demo {\n  padding: 24px;\n}\n\n.line {\n  text-align: center;\n}\n</style>\n\n<style>\n.cascader-popper .el-cascader-menu__wrap {\n  height: 204px;\n}\n</style>\n";