export default "<template>\n  <div class=\"editable-pro-table-demo\">\n    <EditableProTable\n      ref=\"editableProTableRef\"\n      :dataSource=\"dataSource\" \n      :recordCreatorProps=\"recordCreatorProps\" \n      :maxLength=\"5\"\n      :editable=\"editable\"\n      rowKey=\"id\"\n      className=\"editable-table\"\n      :loading=\"loading\"\n      :tableProps=\"{\n        border: true\n      }\"\n      :tableEvents=\"{\n        'selection-change': this.selectionChange\n      }\"\n      :columns=\"columns\"\n      defaultSize=\"small\"\n    >\n      <!-- 2 -->\n      <!-- start -->\n      <template #name2=\"{ form, formItem, recordKey, index }\">\n        <el-input v-model=\"form.name2\" v-bind=\"formItem.fieldProps\"></el-input>\n      </template>\n      <!-- end -->\n    </EditableProTable>\n    <div style=\"margin-top: 10px;\">\n      <el-button @click=\"getFormRef\">getFormRef</el-button>\n      <el-button @click=\"getTableRef\">getTableRef</el-button>\n      <el-button @click=\"validate\">validate</el-button>\n      <el-button @click=\"validateRow\">validateRow</el-button>\n      <el-button @click=\"reset\">reset</el-button>\n      <el-button @click=\"getRowData\">getRowData</el-button>\n      <el-button @click=\"getRowsData\">getRowsData</el-button>\n      <el-button @click=\"setRowData\">setRowData</el-button>\n    </div>\n  </div>\n</template>\n\n<script>\n  import { EditableProTable } from 'element-ui-pro-components'\n  export default {\n    name: 'Demo1',\n    components: {\n      EditableProTable,\n    },\n    computed: {\n      editable() {\n        const { editableKeys } = this\n        return {\n          type: 'multiple',\n          editableKeys,\n          onChange: keys => this.editableKeys = keys,\n          onSave: (recordKey, editRow, originRow) => {\n            console.log('save', recordKey, editRow, originRow)\n            return new Promise(resolve => {\n              setTimeout(() => {\n                resolve(true)\n              }, 1000)\n            })\n          },\n          saveText: '',\n          onDelete: (recordKey, editRow) => {\n            console.log('delete', recordKey, editRow)\n            return new Promise(resolve => {\n              setTimeout(() => {\n                resolve(true)\n              }, 500)\n            })\n          },\n          deleteText: '',\n          onCancel: (recordKey, editRow, originRow) => {\n            console.log('cancel', recordKey, editRow, originRow)\n            return new Promise(resolve => {\n              setTimeout(() => {\n                resolve(true)\n              }, 50)\n            })\n          },\n          cancelText: '',\n          deletePopconfirmMessage: '',\n          onlyOneLineEditorAlertMessage: ''\n        }\n      },\n      columns() {\n        return [\n          {\n            width: 80,\n            type: 'selection',\n            'reserve-selection': true,\n            fixed: 'left'\n          },\n          { \n            width: 240,\n            label: '1',\n            prop: 'name1',\n            valueType: 'input',\n            formItemProps: {\n              rules: [{ required: true, message: '', trigger: 'blur' }]\n            },\n          },\n          {\n            width: 240,\n            label: '2',\n            renderCellHeader: () => <span style=\"color: #F56C6C;\">2</span>,\n            prop: 'name2',\n            valueType: 'slot',\n            fieldProps: {\n              placeholder: 'valueType: slot ',\n              clearable: true\n            },\n          },\n          {\n            minWidth: 240,\n            label: '3',\n            prop: 'name3',\n            renderField: ({ form, formItem, recordKey, index }) => {\n              console.log('renderField', recordKey, index)\n              return <el-input \n                value={ form.name3 } \n                onInput={ val => form.name3 = val } \n                attrs={ formItem.fieldProps }\n              />\n            },\n            fieldProps: {\n              placeholder: 'renderField ',\n              clearable: true,\n            },\n          },\n          {\n            minWidth: 240,\n            label: '3-1',\n            prop: 'name3_1',\n            renderField: ({ form, formItem, recordKey, index }) => {\n              console.log('renderField', recordKey, index)\n              return <el-input \n                value={ form.name3_1 } \n                onInput={ val => form.name3_1 = val } \n                attrs={ formItem.fieldProps }\n              />\n            },\n            formatter: (row, column, cellValue, index) => {\n              return row.name3_1\n            },\n            fieldProps: {\n              placeholder: 'renderField ',\n              clearable: true,\n            },\n          },\n          {\n            minWidth: 240,\n            label: '4',\n            prop: 'name4',\n            valueType: 'input',\n            fieldProps: {\n              placeholder: 'formatter ',\n              clearable: true\n            },\n            formatter: (row, column, cellValue, index) => {\n              return `formatter ${cellValue} loooooooooooooooooooooooooooooooooooooong`\n            },\n            'show-overflow-tooltip': true\n          },\n          {\n            width: 180,\n            label: '',\n            prop: 'type1',\n            valueType: 'select',\n            valueEnum: {\n              '1': '',\n              '2': '',\n              '3': ''\n            },\n          },\n          {\n            width: 180,\n            label: '',\n            prop: 'type2',\n            valueType: 'select',\n            fieldEvents: {\n              change: (val, options) => {\n                console.log('val', val, options)\n              }\n            },\n            optionLoader: () => new Promise(resolve => {\n              setTimeout(() => {\n                resolve([\n                  { label: '', value: '1' },\n                  { label: '', value: '2' },\n                  { label: '', value: '3' },\n                ])\n              }, 100)\n            })\n          },\n          {\n            width: 180,\n            label: '',\n            prop: 'date',\n            valueType: 'date-picker',\n            fieldProps: {\n              valueFormat: 'yyyy-MM-dd'\n            }\n          },\n          {\n            width: 100,\n            label: '',\n            prop: 'status',\n            valueType: 'switch',\n            renderCell: (scope) => <el-switch disabled value={scope.row.status}></el-switch>,\n            editable: (cellValue, row, index) => {\n              console.log('editable', cellValue, row, index)\n              return !cellValue\n            }\n          },\n          {\n            width: 100,\n            label: '',\n            prop: 'readonly',\n            formatter: (row, column, cellValue, index) => {\n              return <span></span>\n            },\n            readonly: true\n          },\n          {\n            width: 180,\n            label: '',\n            valueType: 'option',\n            fixed: 'right',\n            renderCell: (scope, action) => [\n              <el-button type=\"text\" onClick={() => action.startEditable(scope.row.id)}></el-button>,\n              <el-button type=\"text\" onClick={ () => this.dataSource = this.dataSource.filter(item => item.id !== scope.row.id) }></el-button>,\n              <el-button type=\"text\" onClick={() => action.addEditRecord({ id: Math.random().toString().slice(2, 10), name1: scope.row.name1 })}></el-button>,\n            ]\n          }\n        ]\n      }\n    },\n    data() {\n      return {\n        dataSource: undefined,\n        recordCreatorProps: {\n          position: 'top',\n          newRecordType: 'dataSource',\n          record: () => ({\n            // \n            id: Math.random().toString().slice(2, 10),\n            name1: 'hello',\n            name2: undefined,\n            name3: undefined,\n            name3_1: undefined,\n            name4: undefined,\n            type1: undefined,\n            type2: undefined,\n            date: undefined,\n            status: undefined\n          }),\n          createButtonText: '',\n          style: {\n            background: '#d9d9d9'\n          },\n          buttonProps: {\n            type: 'primary'\n          },\n          onlyAddOneLineAlertMessage: '!'\n        },\n        editableKeys: [],\n        loading: false\n      }\n    },\n    created () {\n      this.loading = true\n      setTimeout(() => {\n        this.loading = false\n        const data = [\n          { name1: undefined, name2: '2-1', name3: '3-1', name3_1: '3_1-1', name4: '4-1', id: '1111' },\n          { name1: 'hello js', name2: '2-2', name3: '3-2', name3_1: '3_1-2', name4: '4-2', id: '11111' }\n        ]\n        this.dataSource = data\n      }, 1000)\n    },\n    methods: {\n      selectionChange(selection) {\n        console.log('selection', selection)\n      },\n      getFormRef() {\n        console.log('formRef', this.$refs.editableProTableRef.getFormRef())\n      },\n      getTableRef() {\n        console.log('tableRef', this.$refs.editableProTableRef.getTableRef())\n      },\n      validate() {\n        // \n        const validate = this.$refs.editableProTableRef.validate()\n        validate(valid => {\n          console.log('valid', valid)\n        })\n      },\n      async validateRow() {\n        // \n        const validateRes = await this.$refs.editableProTableRef.validateRow(0)\n        console.log('validateRes', validateRes)\n      },\n      reset() {\n        const reset = this.$refs.editableProTableRef.reset()\n        reset()\n      },\n      getRowData() {\n        const data = this.$refs.editableProTableRef.getRowData(1)\n        console.log('', data)\n        const newData = this.$refs.editableProTableRef.getRowData('11111')\n        console.log('id  11111 ', newData)\n      },\n      getRowsData() {\n        console.log('rowsData', this.$refs.editableProTableRef.getRowsData())\n      },\n      setRowData() {\n        const ref = this.$refs.editableProTableRef\n        ref.setRowData(1, { name1: 'hello javaScript' })\n        ref.setRowData('11111', { name3: undefined })\n      }\n    },\n  }\n</script>\n\n<style scoped>\n</style>\n";