export default "<template>\n  <div class=\"pro-table-demo\">\n    <ProTable\n      ref=\"proTableRef\"\n      :search=\"search\"\n      className=\"pro-table-test\"\n      :dataSource=\"dataSource\"\n      :loading=\"loading\"\n      :total=\"total\"\n      :tableProps=\"{\n        border: true\n      }\"\n      :tableEvents=\"{\n        'selection-change': this.selectChange\n      }\"\n      :columns=\"columns\"\n      :paginationProps=\"{\n        'page-sizes': [10, 20],\n      }\"\n      :paginationMapping=\"{\n        pageKey: 'page',\n        sizeKey: 'size'\n      }\"\n      :initialValues=\"{\n        name4: ''\n      }\"\n      defaultSize=\"small\"\n      :manualRequest=\"true\"\n      :columnSettings=\"columnSettings\"\n      @onParams=\"onParams\"\n      @onSubmit=\"onSubmit\"\n      @onReset=\"onReset\"\n      @onCollapse=\"onCollapse\"\n    >\n      <!-- 3 -->\n      <!-- start -->\n      <template #name3=\"{ form, formItem }\">\n        <el-input v-model=\"form.name3\" v-bind=\"formItem.fieldProps\"></el-input>\n      </template>\n      <!-- end -->\n      <span>\n        <el-button type=\"primary\" @click=\"collapsed = !collapsed\"></el-button>\n        <el-button type=\"primary\" @click=\"reload\"></el-button>\n        <el-button type=\"primary\" @click=\"getTableRef\"></el-button>\n      </span>\n    </ProTable>\n  </div>\n</template>\n\n<script>\n  import { ProTable } from 'element-ui-pro-components'\n\n  const TOTAL_RECORDS = 256\n\n  export default {\n    name: 'Demo1',\n    components: {\n      ProTable,\n    },\n    computed: {\n      search() {\n        return {\n          searchText: '',\n          resetText: '',\n          labelWidth: '105px',\n          defaultCollapsed: false,\n          collapsed: this.collapsed,\n          defaultExpandedRows: 1\n        }\n      },\n      columns() {\n        return [\n          {\n            width: 60,\n            type: 'selection',\n            key: 'selection',\n            disabled: true\n          },\n          { \n            label: '1',\n            prop: 'name1',\n            valueType: 'input',\n          },\n          {\n            label: '2',\n            renderLabel: () => <span style=\"color: #F56C6C\">2</span>,\n            prop: 'name2',\n            valueType: 'input',\n            fieldProps: {\n              placeholder: 'renderLabel'\n            }\n          },\n          {\n            label: '3',\n            renderCellHeader: () => <span style=\"color: #F56C6C;\">3</span>,\n            prop: 'name3',\n            formItemProps: {\n              label: '3'\n            },\n            valueType: 'slot',\n            fieldProps: {\n              placeholder: 'valueType: slot ',\n              clearable: true\n            },\n          },\n          {\n            label: '4',\n            renderField: ({ form, formItem }) => \n              <el-input \n                value={ form.name4 } \n                onInput={val => form.name4 = val } \n                attrs={ formItem.fieldProps }\n              />\n            ,\n            fieldProps: {\n              placeholder: 'renderField ',\n              clearable: true,\n            },\n            renderCell: (scope) => <span> {scope.row.name4}</span>,\n            'show-overflow-tooltip': true,\n            key: 'name4'\n          },\n          {\n            label: '1',\n            prop: 'region1',\n            valueType: 'select',\n            fieldProps: {\n              clearable: true\n            },\n            options: [\n              { label: '', value: 'beijing' },\n              { label: '', value: 'shanghai' },\n            ],\n            initialValue: 'beijing', \n            formatter: (row) => {\n              return ({\n                'beijing': '',\n                'shanghai': ''\n              })?.[row.region1]\n            } \n          },\n          {\n            label: '2',\n            prop: 'region2',\n            valueType: 'select',\n            valueEnum: {\n              'beijing': '',\n              'shanghai': ''\n            },\n          },\n          {\n            label: '3',\n            prop: 'region3',\n            valueType: 'select',\n            valueEnum: new Map([\n              ['beijing', ''],\n              ['shanghai', '']\n            ]),\n          },\n          {\n            label: '4',\n            prop: 'region4',\n            valueType: 'select',\n            optionLoader: () => {\n              return new Promise(resolve => {\n                setTimeout(() => {\n                  resolve([\n                    { label: '', value: 'beijing' },\n                    { label: '', value: 'shanghai' },\n                  ])\n                }, 1000)\n              })\n            },\n            formatter: (row) => {\n              return ({\n                'beijing': '',\n                'shanghai': ''\n              })?.[row.region4]\n            } \n          },\n          {\n            label: 'hide in search',\n            prop: 'search',\n            valueType: 'input',\n            hideInSearch: true\n          },\n          {\n            label: 'hide in table',\n            prop: 'table',\n            valueType: 'input',\n            hideInTable: true\n          },\n          {\n            width: 120,\n            label: '',\n            fixed: 'right',\n            renderCell: (scope) => [\n              <el-button type=\"text\"></el-button>,\n              <el-button type=\"text\"></el-button>\n            ],\n            key: 'option',\n          }\n        ]\n      }\n    },\n    data() {\n      return {\n        collapsed: undefined,\n        dataSource: [],\n        loading: false,\n        total: 0,\n        columnSettings: {\n          resetText: '',\n          settingText: '',\n        }\n      }\n    },\n    methods: {\n      onParams(params) {\n        console.log('params', params)\n        const data = Array.from({ length: TOTAL_RECORDS }, () => ({\n          name1: `1 ${Math.random().toString().slice(2, 6)}`,\n          name2: `2 ${Math.random().toString().slice(2, 6)}`,\n          name3: `3 ${Math.random().toString().slice(2, 6)}`,\n          name4: `4 ${Math.random().toString().slice(2, 6)}`,\n          region1: 'beijing',\n          region2: 'shanghai',\n          region3: 'shanghai',\n          region4: 'beijing',\n        }))\n        const { page, size } = params\n        this.loading = true\n        setTimeout(() => {\n          this.loading = false\n          this.dataSource = data.slice((page - 1) * size, page * size)\n          this.total = TOTAL_RECORDS\n        }, 1000)\n      },\n      onSubmit(params) {\n        console.log('onSubmit', params)\n      },\n      onReset() {\n        console.log('onReset')\n      },\n      onCollapse(collapsed) {\n        this.collapsed = collapsed\n        console.log(`${collapsed ? '' : ''}`)\n      },\n      selectChange(rows) {\n        console.log('rows', rows)\n      },\n      reload() {\n        this.$refs.proTableRef.reload()\n      },\n      getTableRef() {\n        const ref = this.$refs.proTableRef.getTableRef()\n        console.log('ref', ref)\n      }\n    },\n  }\n</script>\n\n<style scoped>\n.pro-table-demo {\n  padding: 24px;\n}\n</style>\n";